<template>
  <div class="p5SingleParticle">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      particle: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);

        this.particle = new Particle(p.createVector(p.width / 2, p.height / 2));
      };

      p.draw = () => {
        p.background(255);

        this.particle.run();
      };

      class Particle {
        constructor(l) {
          this.location = l.copy();
          this.velocity = p.createVector(p.random(-1, 1), p.random(-2, 0));
          this.acceleration = p.createVector(0, 0.05);
          this.lifespan = 1.0;
        }

        run() {
          this.update();
          this.display();
        }

        update() {
          this.velocity.add(this.acceleration);
          this.location.add(this.velocity);
          this.lifespan -= 0.01;
        }

        display() {
          p.stroke(`rgba(57, 81, 105, ${this.lifespan})`);
          p.strokeWeight(2);
          p.fill(`rgba(66, 185, 131, ${this.lifespan})`);
          p.ellipse(this.location.x, this.location.y, 32, 32);
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5SingleParticle {
  user-select: none;
}
</style>
