<template>
  <div class="p5PolarToCartesian">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      mover: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);
        p.background(255);

        this.mover = new Mover();
      };

      p.draw = () => {
        this.mover.update();
        this.mover.display();
      };

      class Mover {
        constructor() {
          this.r = 0;
          this.theta = 0;
          this.location = p.createVector(0, 0);
        }

        display() {
          p.noStroke();
          p.fill("#42b983");
          p.ellipse(
            this.location.x + p.width / 2,
            this.location.y + p.height / 2,
            16,
            16
          );
        }

        update() {
          // polar coords (r, theta) are converted to cartesian (x, y)
          this.location.x = this.r * p.cos(this.theta);
          this.location.y = this.r * p.sin(this.theta);

          this.r += 0.2;
          this.theta += 0.05;
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5PolarToCartesian {
  user-select: none;
}
</style>
