<template>
  <div class="p5PendulumSwing">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      myPendulum: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);

        this.myPendulum = new Pendulum(
          p.createVector(p.width / 2, p.height / 2),
          125
        );
      };

      p.draw = () => {
        p.background(255);

        this.myPendulum.go();
      };

      class Pendulum {
        constructor(o, r) {
          // Location of arm origin
          this.origin = o.copy();

          // Length of arm
          this.r = r;

          // Location of bob
          this.location = p.createVector();

          // Pendulum arm angle
          this.angle = p.PI / 4;

          // Angle velocity
          this.aVelocity = 0.0;

          // Angle acceleration
          this.aAcceleration = 0.0;

          // Arbitrary damping amount (pendulum slows over time)
          this.damping = 0.995;
        }

        go() {
          this.update();
          this.display();
        }

        update() {
          let gravity = 0.4;

          // angular acceleration
          this.aAcceleration = ((-1 * gravity) / this.r) * p.sin(this.angle);

          // standard angular motion algorithm
          this.aVelocity += this.aAcceleration;
          this.angle += this.aVelocity;

          // apply damping
          this.aVelocity *= this.damping;
        }

        display() {
          // polar to cartesian coords give us bob location relative to origin
          this.location.set(
            this.r * p.sin(this.angle),
            this.r * p.cos(this.angle),
            0
          );
          this.location.add(this.origin);

          p.stroke(0);
          p.line(
            this.origin.x,
            this.origin.y,
            this.location.x,
            this.location.y
          );
          p.fill("#42b983");
          p.ellipse(this.location.x, this.location.y, 16, 16);
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5PendulumSwing {
  user-select: none;
}
</style>
